








































    import Vue from 'vue';
    import LoadingSpinner from '../partials/LoadingSpinner.vue';
    import MissionPreviewView from '../partials/MissionPreviewView.vue';

    export default Vue.extend({
        data() {
            return {
                assignInProgress: false,
                selectError: false,
                mission: null
            };
        },

        components: {
            LoadingSpinner,
            MissionPreviewView
        },

        async created() {
            try {
                this.mission = await this.$api.get('/missions/next').then(response => response.data);
            } catch (e) {
                this.$router.push({
	                name: this.$config('routes.waiting')
                });
            }
        },

        methods: {
            async assignMission() {
                if (this.assignInProgress) {
                    return;
                }
                this.assignInProgress = true;

                try {
                    await this.$store.dispatch('missions/assignMission');
                } catch (err) {
                    this.selectError = true;
                    this.assignInProgress = false;
                }
            }
        }
    });
